      h1 Turismo accesible     

    .row.justify-content-center.align-items-center.mb-4 
      .col-lg-8(data-aos="fade-right")
        p.mb-4 El turismo accesible nace por la necesidad que posee el sector turstico de atender a un grupo considerable de personas que les es ms difcil participar de las actividades tursticas ya sea por sus condiciones fsicas o mentales, pero el ideal de este tipo de turismo es que toda persona, sin importar quien sea y en las condiciones que se encuentre, pueda realizar la actividad turstica ms fcilmente; teniendo como principal objetivo las poblaciones o el tipo de poblacin ms vulnerable, como lo pueden ser los jvenes, los adultos mayores y las personas en situacin de discapacidad. Se estima que en el mundo hay alrededor de mil millones de personas que tienen algn tipo de discapacidad y de acuerdo con el Ministerio de Salud  y Proteccin Social (2017) en Colombia es el 6.1% de la poblacin, estas cifras son relevantes ya que esta poblacin al igual que el resto de personas tienen deseos de viajar y disfrutar de las diferentes actividades que se desarrollan en los destinos tursticos, pero es indispensable que los atractivos tursticos y los mismos prestadores de servicios tursticos identifiquen y eliminen las barreras fsicas  y las mentales para que esta poblacin pueda acceder y disfrutar  de los servicios tursticos ofrecidos en los distintos lugares. Finalmente, vale la pena aclarar que los beneficios de la accesibilidad no se enfocan exclusivamente en las poblaciones con discapacidad, sino que estn orientados a toda la poblacin en general.
      .col-lg-4.col-md-6.col-6(data-aos="fade-left")
        figure.desaparece
          img(src='@/assets/curso/tema3/1.jpg', alt='Texto que describa la imagen')

    .row.justify-content-center.align-items-center.mb-4
      .col-lg-4.col-md-6.col-6(data-aos="fade-right")
        figure.mb-4
          img(src='@/assets/curso/tema3/2.jpg', alt='Texto que describa la imagen')
      .col-lg-8(data-aos="fade-left")
        p.mb-4 En el Manual de Turismo accesible <b> <FONT COLOR="#FD6300"> Turismo para todos </FONT> </b> publicado por el Ministerio de Industria Comercio y Turismo (MINCIT, 2019), se exponen las siguientes definiciones:
        .cajon.color-primario(style='background-color: #FFD667').p-4.mb-4 
          p.mb-0 Turismo accesible es el conjunto integral de actividades y recorridos dentro de entornos e itinerarios que se presentan en los viajes, distintos al entorno habitual, de una o varias personas en condiciones de seguridad, confortabilidad y autonoma (p. 26).
        p.mb-4 La Accesibilidad y el Diseo Universal (A.D.U) es el arte de crear y adaptar los entornos, servicios y productos a las personas, para que sean utilizables por todos de la forma ms autnoma y natural posible (MINCIT, 2019, p.28).
        p.mb-0 Contexto Internacional:  es indudable que las regiones del mundo ms slidas desde el punto de vista econmico y socialmente evolucionadas, han descubierto desde hace tiempo la necesidad y conveniencia de desarrollar polticas de turismo accesible.  En el caso europeo se han establecido importantes polticas de incentivo y estmulo a la inversin privada para el desarrollo del turismo accesible.

    .bg_grad-13
      .row.align-items-center.mb-4
        .col-lg-1.col-md-2.col-3(data-aos="fade-right")
          figure
            img(src='@/assets/curso/tema3/icon1.svg', alt='Texto que describa la imagen')
        .col-lg-11.col-md-10.col-9(data-aos="fade-left")
          p.mb-0 Un importante avance ligado a la evolucin de los medios electrnicos de comunicacin es el diseo y publicacin en Internet de guas tursticas <em>on-line</em> especficas sobre turismo accesible, en algunos casos por regiones geogrficas, en otros por pases y en numerosas ocasiones hasta por ciudades. El turista con discapacidad puede acceder a ellas desde cualquier computadora en forma gratuita, consultar instalaciones accesibles, servicios, excursiones y actividades, tarifas, efectuar reservas, etc. (Fuentes, 2015, p. 23).

      .row.justify-content-center.align-items-center.mb-4
        .col-lg-8(data-aos="fade-right")
          p.mb-4 Uno de los pases pioneros en turismo accesible es Espaa, donde el gobierno y diversas organizaciones se han preocupado en desarrollar e incluir a todo tipo de personas en las actividades tursticas. Es de admirar que la mayor parte de los parques nacionales espaoles y sus comunidades autnomas y ciudades, cuentan con programas, infraestructura y servicios para el turismo accesible, como asimismo guas en internet que promueven los diferentes destinos tursticos.
          p.mb-4 Un ejemplo es el Portal Internacional de Turismo Accesible para personas con movilidad reducida del SINPROMI (Sociedad Insular para la Promocin del Minusvlido) de Tenerife (Islas Canarias) en el marco de su Programa <b> <FONT COLOR="#FD6300"> "Turismo y Cultura para Todos" </FONT> </b> (Sociedad Insular para la promocin de las personas con discapacidad. 2021).  En este portal se hace una caracterizacin de diferentes servicios y atractivos tursticos, como, por ejemplo, alojamiento, playas, instalaciones de ocio y cultura, bares, restaurantes, transportes y lugares de inters entre otros; muestra informacin general del lugar y a la vez le da una valoracin en tres rangos: accesible, practicable e inaccesible; con esta informacin el turista puede identificar de manera anticipada las condiciones que presenta el lugar.
        .col-lg-4.col-md-8.col-8(data-aos="fade-left")
          figure
            img(src='@/assets/curso/tema3/fig1.svg', alt='Texto que describa la imagen')

      p.mb-4(data-aos="fade-right") Otra pgina de ejemplo es <b> <FONT COLOR="#FD6300"> Puedo Viajar, </FONT> </b> que nace en torno al turismo accesible para personas con discapacidad, se trata de un proyecto en internet que identifica los diferentes destinos tursticos accesibles en Espaa, sugiere rutas y establecimientos, adems de tener la opcin de planificar el viaje; tambin cuenta con algunas guas descargables de turismo accesible en las principales ciudades europeas, como por ejemplo Londres, Pars, Ibiza, Berln, Venecia.  

      .row.justify-content-center.align-items-center.mb-4
        .col-lg-4.col-md-8.col-8(data-aos="fade-right")
          figure.mb-4
            img(src='@/assets/curso/tema3/3.jpg', alt='Texto que describa la imagen')
        .col-lg-8(data-aos="fade-left")
          p.mb-4 En cuanto a la Unin Europea es indispensable destacar el fundamental rol desempeado por esta a travs de la Comisin Europea, ya que han formulado polticas en el sector de servicios tursticos enfocadas en la promocin, afianzamiento y evolucin del turismo accesible. Al respecto, ha desarrollado y publicado -entre otros- los siguientes documentos:
          .cajon.color-secundario(style='background-color: #B7CEAC').p-4.mb-4
            ul.lista-ul--color.mb-0
              li
                i.fas.fa-paper-plane 
                p.mb-0 Haciendo accesible a Europa a los turistas con discapacidades.
              li
                i.fas.fa-paper-plane 
                p.mb-0 Gua <em>on-line</em> de viaje para turistas discapacitados.
              li
                i.fas.fa-paper-plane 
                p.mb-0 Criterios uniformes para la accesibilidad de lugares tursticos e infraestructura para personas discapacitadas.
      
      .row.align-items-center.mb-4
        .col-lg-6(data-aos="fade-right")
          p.mb-4 Adems, la Comisin Europea ha desarrollado proyectos especficos tales como "Tourism for All" (Turismo para Todos) con la intervencin de numerosos pases de la comunidad (en este caso: Austria, Alemania, Italia, Luxemburgo, Noruega, Suecia, Suiza, Holanda y Espaa).  Existen publicadas en Internet guas de accesibilidad para la casi totalidad de los pases europeos, sus regiones y ciudades. Se destacan los casos de: Austria, Blgica, Dinamarca, Finlandia, Francia, Alemania, Grecia, Islandia, Irlanda, Italia, Liechtenstein, Luxemburgo, Holanda, Noruega, Portugal, Suecia, Reino Unido.  Tambin se destaca en Italia la pgina web del proyecto, Turismo per tutti, una iniciativa de la Direccin General de Turismo del Ministerio de Actividades Productivas italiano para promover y difundir el turismo accesible.   
        .col-lg-6(data-aos="fade-left")
          figure
            img(src='@/assets/curso/tema3/4.jpg', alt='Texto que describa la imagen')
      p.mb-4(data-aos="fade-right") En Estados Unidos tambin se destacan algunas iniciativas que se han dado debido a que este pas tiene unas cifras elevadas en cuanto a personas con discapacidad a nivel mundial. La mayora de sus estados, cuentan con guas de accesibilidad. En 1988 se realiz la Gua de Viajes para Personas con Discapacidades del estado de Virginia y es relevante porque fue una de las primeras publicaciones que mostraba la oferta de turismo accesible del lugar. La USAID (Agencia para el Desarrollo Internacional) lanz una aplicacin de turismo accesible en el caribe para mviles llamada RD4ALL, tiene como finalidad la promocin del turismo accesible en Centroamrica, especialmente Repblica Dominicana.

      .row.align-items-center.mb-4
        .col-lg-6(data-aos="fade-right")
