            img(src='@/assets/curso/tema3/5.jpg', alt='Texto que describa la imagen')
        .col-lg-6(data-aos="fade-left")
          p.mb-0 En referencia a Sudamrica hay pases que por su tradicin turstica le han apostado al turismo accesible estos son los casos de Argentina, Brasil, Chile y Per. Puntualmente Per se destac en el ao 2000 cuando lanzaron un manual de turismo accesible para los prestadores de servicios tursticos, este manual se llama Manual para la Atencin del Turista con Discapacidad de Promper, y se ha convertido en un valioso aporte para la realizacin de turismo accesible en Suramrica.  En Centroamrica, Mxico y Costa Rica, al tener un desarrollo turstico grande, tambin han mostrado inters por esta parte del turismo y han creado normas y documentos constituyndose en modelos para la regin; por ejemplo, existe la Red Costarricense de Turismo Accesible, que es una asociacin sin nimo de lucro que busca capacitar en referencia a este tema a los prestadores de servicios tursticos del pas.
          
