          p.mb-4 Antes de pensar en realizar recorridos tursticos y atender a personas en situacin de discapacidad es vital tener en cuenta una serie de normas y parmetros los cuales permiten comprender la forma de ver y sentir de las personas que tienen algn tipo de discapacidad. Los siguientes son algunos de los criterios para tenerlos presentes siempre, no solo cuando se realice un recorrido turstico o una guianza, si no siempre que se tenga contacto con alguna persona en situacin de discapacidad.
          ul.lista-ul--color.mb-0
            li
              i.fas.fa-paper-plane 
              p.mb-0 La persona en situacin de discapacidad no siempre tiene retardo.
            li
              i.fas.fa-paper-plane 
              p.mb-0 La persona en situacin de discapacidad no siempre es inmadura.
            li
              i.fas.fa-paper-plane 
              p.mb-0 La discapacidad no la hace necesariamente ms frgil.
            li
              i.fas.fa-paper-plane 
              p.mb-0 La persona en situacin de discapacidad no necesita compasin.
            li
              i.fas.fa-paper-plane 
              p.mb-0 La persona en situacin de discapacidad necesita, como todos, respeto.

    p.mb-4(data-aos="fade-right") Reconociendo lo anterior, y siempre teniendo el firme propsito de aplicarlo en cualquier momento en el cual se tenga contacto con una persona en situacin de discapacidad, es recomendable tambin tener presente y aplicar el siguiente declogo de la Asociacin Colombiana para el Desarrollo de Personas con Discapacidad (ASCOPAR, s.f.), que hace ms fcil el poder relacionarse con este tipo de poblacin: 

    .row.justify-content-center.align-items-center.mb-4
      .col-lg-8(data-aos="fade-right")
        .slyderB_bg_02.py-4.px-4.mb-4
          SlyderA
            .row.align-items-center.justify-content-center.p-4
              .col-lg-1.col-md-2.col-2
                figure.mb-2
                  img(src='@/assets/curso/tema3/vec1.svg', alt='Texto que describa la imagen')
              .col-lg-11.col-md-10.col-10
                p.mb-2 Respetar a las personas con discapacidad.
              .col-lg-1.col-md-2.col-2
                figure.mb-2
                  img(src='@/assets/curso/tema3/vec1.svg', alt='Texto que describa la imagen')
              .col-lg-11.col-md-10.col-10
                p.mb-2 Tratar como iguales a las personas con discapacidad en los distintos ambientes (escuela, trabajo, calle, etc.).
              .col-lg-1.col-md-2.col-2
                figure.mb-2
                  img(src='@/assets/curso/tema3/vec1.svg', alt='Texto que describa la imagen')
              .col-lg-11.col-md-10.col-10
                p.mb-2 Tomar conciencia, que la discapacidad es solo una condicin ms del ser humano, como ser alto, bajo, gordo, flaco, negro, blanco, indio o mestizo.
              .col-lg-1.col-md-2.col-2
                figure.mb-2
                  img(src='@/assets/curso/tema3/vec1.svg', alt='Texto que describa la imagen')
              .col-lg-11.col-md-10.col-10
                p.mb-2 Preguntar a una persona con discapacidad, antes de actuar, si quiere que lo ayude y sobre todo cmo lo puedo ayudar.
              .col-lg-1.col-md-2.col-2
                figure.mb-2
                  img(src='@/assets/curso/tema3/vec1.svg', alt='Texto que describa la imagen')
              .col-lg-11.col-md-10.col-10
                p.mb-2 No invadir los espacios reservados para personas con discapacidad en lugares pblicos.

            .row.align-items-center.justify-content-center.p-4
              .col-lg-1.col-md-2.col-2
                figure.mb-2
                  img(src='@/assets/curso/tema3/vec1.svg', alt='Texto que describa la imagen')
              .col-lg-11.col-md-10.col-10
                p.mb-2 Eliminar de mi vocabulario los trminos paralticos, invlido, minusvlido, parapljico y discapacitado, cuando me refiera a una persona con discapacidad.
              .col-lg-1.col-md-2.col-2
                figure.mb-2
                  img(src='@/assets/curso/tema3/vec1.svg', alt='Texto que describa la imagen')
              .col-lg-11.col-md-10.col-10
                p.mb-2 Utilizar el trmino personas con discapacidad para referirme a ellos.
              .col-lg-1.col-md-2.col-2
                figure.mb-2
                  img(src='@/assets/curso/tema3/vec1.svg', alt='Texto que describa la imagen')
              .col-lg-11.col-md-10.col-10
                p.mb-2 Canalizar a las personas con discapacidad a entidades que les permitan un desarrollo para mejorar su calidad de vida.
              .col-lg-1.col-md-2.col-2
                figure.mb-2
                  img(src='@/assets/curso/tema3/vec1.svg', alt='Texto que describa la imagen')
              .col-lg-11.col-md-10.col-10
                p.mb-2 No ocasionar conflictos ni enfrentamientos directos con personas que no tienen esta cultura de respeto y trato digno de las personas con discapacidad.
              .col-lg-1.col-md-2.col-2
                figure.mb-2
                  img(src='@/assets/curso/tema3/vec1.svg', alt='Texto que describa la imagen')
              .col-lg-11.col-md-10.col-10
                p.mb-2 Ante la duda de cmo tratar las discapacidades y las necesidades de una persona con discapacidad, contactar a instituciones especializadas para recibir la informacin correcta.          
      .col-lg-4(data-aos="fade-left")
        figure.desaparece
          img(src='@/assets/curso/tema3/img2.jpg', alt='Texto que describa la imagen')

    .row.justify-content-center.align-items-center.mb-4
      .col-lg-4.col-md-8.col-8(data-aos="fade-right")
        figure.mb-4
          img(src='@/assets/curso/tema3/vec2.svg', alt='Texto que describa la imagen')
      .col-lg-8(data-aos="fade-left")
        p.mb-4 Teniendo en cuenta el protocolo de servicio para el turismo accesible de turistas y visitantes jvenes, adultos mayores y personas en situacin de discapacidad (2011) respecto a la atencin de ellas, a continuacin, se relacionan algunos parmetros para la atencin de turistas que presentan diferentes condiciones de discapacidad. Inicialmente se dan unas recomendaciones que el gua de turismo debe considerar antes y durante la realizacin de un recorrido guiado y finalmente, se abordan las recomendaciones especficas para cada una de las siguientes discapacidades: 
        ul.lista-ul--color.mb-0
          li
            i.fas.fa-paper-plane 
            p.mb-0 Personas en sillas de ruedas o con movilidad reducida.
          li
            i.fas.fa-paper-plane 
            p.mb-0 Personas en situacin de discapacidad auditiva.
          li
            i.fas.fa-paper-plane 
            p.mb-0 Personas en situacin de discapacidad visual.
          li
            i.fas.fa-paper-plane 
            p.mb-0 Personas con discapacidad visual y auditiva.
          li
            i.fas.fa-paper-plane 
            p.mb-0 Personas en situacin de discapacidad cognitiva/intelectual.

    p.mb-4(data-aos="fade-right") Para realizar recorridos guiados a turistas en situacin de discapacidad, es indispensable saber previamente al recorrido el tipo de discapacidad que presenta el turista y as mismo se pueden utilizar las tcnicas ms adecuadas, pero en la mayora de las discapacidades hay tcnicas de guionaje que pueden servir para todo tipo de discapacidad, algunas de estas pueden ser las que se nombran a continuacin.

    .bloque-texto-g.color-primario.p-3.p-sm-4.p-md-5.mb-4(data-aos="fade-up")
      .bloque-texto-gg__img(
        :style="{'background-image': `url(${require('@/assets/curso/tema3/img3.jpg')})`}"
      )
      .bloque-texto-gg__texto.p-5
        p.mb-0 El gua de turismo debe tener la capacidad de observar y escuchar al turista en situacin de discapacidad y darle la posibilidad a la persona de solicitar asistencia o ayuda en caso de que lo requiera. Es importante que el gua de turismo le pregunte al turista o la familia, si requiere ayuda en el caso que el turista requiera de ayuda, se debe colaborar totalmente con lo solicitado por el turista.

    p.mb-4(data-aos="fade-right") En los casos en que se realice la prestacin del servicio de guionaje a ncleos familiares en donde exista alguna persona en situacin de discapacidad, se debe alentar a que participe activamente sin hacer que dicha participacin sea obligatoria.  Es necesario dejar que la persona en situacin de discapacidad, segn el caso, sea independiente y dejar que ella haga las cosas por s sola, adems de ello se recomiendan los siguientes tips para atender esta realidad:

    .row.justify-content-center.align-items-center.mb-4
      .col-lg-5.col-md-6.col-6(data-aos="fade-right")
        figure.mb-4
          img(src='@/assets/curso/tema3/img4.jpg', alt='Texto que describa la imagen')
      .col-lg-7(data-aos="fade-left")
        AcordionA.mb-2(tipo="a" clase-tarjeta="tarjeta tarjeta-azul")
          .row(titulo="En el recorrido")
            p.mb-0 Cuando se est desarrollando un recorrido guiado, es fundamental que el gua le hable directamente a la persona en situacin de discapacidad y no a su acompaante, ya que es un error comn el explicarle al acompaante algunos sucesos o darle informacin puntual para este la retransmita a la persona que presenta una discapacidad. Se debe comprender que no todas las personas que presentan alguna discapacidad son iguales, cada una de ella presenta capacidades y desarrolla destrezas diferentes.  

          .row(titulo="En la planeacin del recorrido")
            p.mb-0 Aprender los nombres de memoria si es un grupo pequeo y el nombre del tour o paquete si es un grupo grande.

          .row(titulo="Preparar su indumentaria")
            p.mb-0 En el momento de la planeacin del recorrido guiado, el gua de turismo debe identificar la cantidad de ayuda que necesitar para el desarrollo del servicio contratado, en algn caso se puede requerir de ayuda adicional, este personal tambin debe estar en la capacidad para atender al turista con seguridad y velar tambin por la seguridad de l mismo.  

          .row(titulo="En la comunicacin")
