      .h2 2.1 Tipologa de grupos 

    .row.align-items-center.mb-4
      .col-lg-5(data-aos="fade-right")
        p.mb-4 Hablar de grupo de personas es relacionarlas con identidad, comunicacin, relaciones interpersonales, cumplimiento de normas, distribucin de tareas, objetivo comn, e intercambio de ideas, opiniones y/o conceptos; aspectos que contribuyen a elevar la productividad de lo planeado. La sociedad actual presenta necesidades y caractersticas peculiares en las que el trabajo en grupo es cada vez ms comn y necesario. Profesionales de los diferentes campos laborales encuentran sumamente til y prctico el trabajo grupal, ya sea por razones econmicas, de tiempo o simplemente porque en muchos casos sus efectos son ms positivos que los del trabajo individual (Servicio Nacional de aprendizaje SENA., 2018)
      .col-lg-7(data-aos="fade-left")
