    p.mb-4(data-aos="fade-right") El gua al estar a cargo de un grupo de usuarios debe caracterizarlos y clasificarlos para su respectiva atencin, a continuacin, un ejemplo (ver figura 5):

    .row.justify-content-center.mb-2
