            li
              i.fas.fa-paper-plane 
              p En los grupos homogneos, las edades, cultura, clase social y motivaciones son muy similares y el lder formal.
            li
              i.fas.fa-paper-plane 
              p.mb-0 En los grupos grandes o de ms de 30 usuarios, se forman subgrupos. Se acentan las caractersticas de la personalidad de cada individuo que lo componen; los tmidos sern ms tmidos, los graciosos ms graciosos. Exigen una organizacin impecable, sin mrgenes para la improvisacin.
            li
              i.fas.fa-paper-plane 
