            li
              i.fas.fa-paper-plane 
              p.mb-0 Los grupos conformados por estudiantes por lo general tienen edades similares, tienen por lo general un lder formal, el profesor, al que hay que tomar en consideracin. El problema radica en las diferencias de intereses del profesor y los estudiantes.
            li
              i.fas.fa-paper-plane 
              p.mb-0 Tercera edad, son los grupos conformados por jubilados cuya motivacin no es tan slo la cultura, si no que juega un papel muy importante el pasrselo bien.  Juega un papel muy importante la actitud de servicio del gua, ms que sus conocimientos, requiere de paciencia, don para escuchar, trato carioso y que les amenice el viaje, as ser su gua ideal.  Por lo general no les gusta disponer de tiempo libre, sino que prefieren que sea el gua quien lo organice.
            li
              i.fas.fa-paper-plane 
