          p.mb-4 En caso de que el grupo sea heterogneo, uno de los objetivos del gua es fomentar la convivencia, para suavizar las percepciones de inseguridad e insatisfaccin que genera el "primer da de viaje" cuando nadie conoce a nadie.  Por tanto, el gua debe convertirse en el catalizador de reacciones, evitar que algn miembro sea ignorado, aislado o rechazado integrar a todos y prestando especial atencin a los introvertidos, desarrollar estrategias que tiendan a la integracin del grupo, despertando el inters de uno hacia otros y consiguiendo que se relacionen de manera igualitaria y por supuesto establecer reglas que faciliten la convivencia. (FIR Consultora & Servicio Andaluz de Empleo 2018)(ver figura 6).
