    #t_2_2.titulo-segundo.color-acento-contenido(data-aos="fade")
      .h2 2.2 Tcnicas para atencin de grupos guiados 

    p.mb-4(data-aos="fade-right") Con el conveniente uso de tcnicas para la atencin de grupos se previenen problemas de interrelacin o se evita que estos alcancen dimensiones superiores. Entre ellas estn: 

    .row.align-items-center.mb-4
      .col-lg-6.mb-4(data-aos="fade-right")
        LineaTiempoD.color-secundario
          p.text-small(numero="1" titulo="Las explicaciones") El nivel y la calidad de las explicaciones del gua deben ser acordes al nivel del grupo, si estas son demasiado bajas, puede sentir un rechazo generalizado del grupo. Si es necesario pida que le repitan la pregunta para pensar la respuesta y dar una respuesta adecuada. No coloque en conocimiento de los usuarios que se trata de la primera vez, porque lesiona seriamente la confianza de los clientes.
            
          p.text-small(numero="2" titulo="La educacin") Frente a un mal entendido o un inconveniente con el grupo, una de las reglas bsicas para tratar con cualquier tipo de clientes es la educacin, trate a todos los clientes con gran respeto y tacto. Si los inconvenientes se dan entre los pasajeros, busque solventarlos por todos los medios, principalmente cntrense en calmar los nimos.
            
          p.text-small(numero="3" titulo="Actitud conciliadora") La actitud, tranquila y serena del gua le transmite seguridad al grupo y facilita el dominio de este cuando tiene una actitud positiva y de aceptacin del grupo. El estado de nimo del gua influye sobre el grupo y a la inversa. Recuerde que el comportamiento grupal tiende a la imitacin; si el gua es atento con su equipo, el conductor los proveedores, todos tendern a hacer lo mismo entre s.

          p.text-small(numero="4" titulo="Trato preferencial") Como gua evite demostrar un trato preferencial con un usuario o una cercana personal que genere conflictos entre el grupo. Por esta razn se recomienda no recibir propinas, para evitar tener conflicto de intereses. Es aconsejable relacionarse con todos los subgrupos y a la vez no identificarse con ninguno.
            
          p.text-small(numero="5" titulo="Independencia") Demuestre como gua ser independiente del grupo y de la agencia; primero, para poder ver qu hay de objetivo y real en las quejas y segundo, para que sus opiniones y decisiones sean consideradas ecunimes. Evite someter a cualquier decisin a votacin, en especial el itinerario porque resta importancia a la planeacin detallada que se realiz del servicio. 
      .col-lg-6(data-aos="fade-left")
        figure.mb-2
          img(src='@/assets/curso/tema2/fig5.svg', alt='Texto que describa la imagen')
    p.mb-4(data-aos="fade-right") Al gua de turismo le corresponde velar por el cumplimiento de las normas y, en caso de que estas no existan, debe establecerlas, para facilitar la convivencia, algunas de las reglas ms importantes son: 

    .row.justify-content-center.mb-4(data-aos="fade-up")
      .col-lg-4.col-md-8.col-8.mb-4
