            .tarjeta-flip__contenido.colorama1.p-5.p-xl-5
              h4 Puntualidad
              p.mb-4 Establezca la diferenciacin para quien es puntual con quien no lo es.
              p.mb-0 Presente esta prctica como una forma de mostrar respeto a los dems.

      .col-lg-4.col-md-8.col-8.mb-4
