              h4 Los cambios
              p.mb-4 Explique ampliamente el itinerario y qu incluye el servicio antes de iniciar.
              p.mb-4 Aclare si hay o no la posibilidad de cambios. 
              p.mb-4 Sea claro en la disponibilidad del men y organcelo previamente a la llegada del restaurante.
    
    .bg_grad-11
      .row.mb-4
        .col-auto(data-aos="fade")
          .sub.p-2
            h4.mb-0.text-center La puntualidad

      .row.align-items-center.mb-4
        .col-lg-4(data-aos="fade-right")
          figure.mb-4
            img(src='@/assets/curso/tema2/img4.jpg', alt='Texto que describa la imagen')
        .col-lg-8(data-aos="fade-left")
          p.mb-0 Es un aspecto fundamental que el gua muestre la incomodidad de esperar a alguien sin una causa justificada. La forma adecuada de plantear la puntualidad es basarla en el respeto a los dems. En caso de retraso de algn participante, debe estar atento a recibir a los que llegan tarde, interesndose por lo que les ha sucedido:  si han sufrido algn percance, muestre su inters por lo sucedido antes que llamarle la atencin frente al grupo sin averiguar lo ocurrido.   Si no les ha sucedido nada de gravedad, de manera educada en privado, haga ver las consecuencias en el itinerario, para que sienta la necesidad de pedir una disculpa y evitar repetir la tardanza.  Si a pesar de las llamadas de atencin en privado continan llegando tarde, es conveniente buscar un motivo que obligue a ello, eximiendo al gua de responsabilidad, por ejemplo, la imposibilidad de estar esperndolos en determinados lugares por las reglas del destino o atractivo, el horario inamovible de un vuelo, entre otras.  Para dar mayor credibilidad se procede a dar el precio y hora de los transportes pblicos hacia el prximo destino. Si se amenaza, hay que ser conscientes de que se debe cumplir, por tanto, solo recurrir a ella cuando no haya mucha dificultad en llegar al destino, por ejemplo, en los traslados dentro de la misma ciudad.

      .row.mb-4
        .col-auto(data-aos="fade")
          .sub.p-2
            h4.mb-0.text-center Responsabilidad de los pasajeros

      .row.align-items-center.mb-4
        .col-lg-8(data-aos="fade-right")
          p.mb-4 La llamada de despertar en el alojamiento se hace con el propsito de despertar a los viajeros con una llamada desde la recepcin, pero cuando por algn motivo la recepcin olvida despertar a un viajero, el viajero cree no ser culpable de su tardanza. El gua debe dejar claro que es responsabilidad de los viajeros el despertarse y que la llamada tan slo constituye un complemento para asegurarse. Por otro lado, respecto a las pertenencias, es importante aclarar que ni el gua, ni el equipo de trabajo como gua o proveedores pueden ser custodios de estas. Esto ayuda a que el usuario sea consciente de lo que realmente necesita. En caso de que se requiera que los usuarios dejen algunos elementos personales en el vehculo, es ideal dejarlos en custodia o ubicarlos de tal forma que queden ocultos de los ladrones o tambin ubicarlos en la bodega o bal, as de esta manera en caso de infortunio como un robo puedan ser cubiertos por el seguro de viajes, el cual tambin es responsabilidad del viajero. 
        .col-lg-4(data-aos="fade-left")
          figure.mb-2
            img(src='@/assets/curso/tema2/img5.jpg', alt='Texto que describa la imagen')

      .row.mb-4
        .col-auto(data-aos="fade")
          .sub.p-2
            h4.mb-0.text-center Los cambios

      .row.align-items-center.mb-4
        .col-lg-4(data-aos="fade-right")
          figure.mb-2
            img(src='@/assets/curso/tema2/fig6.svg', alt='Texto que describa la imagen')
        .col-lg-8(data-aos="fade-left")
          p.mb-0 En los meses de mayor afluencia turstica, ni a los meseros, ni a los cocineros u otros prestadores de servicios les agrada tener que realizar cambios de ltima hora en lo ofertado y aprobado. El cambio de un men, por ejemplo, supone un costo adicional para el restaurante que ya ha preparado el total de mens reservados en funcin de lo acordado; aunque es posible solicitar cambios siempre y cuando no afecte ampliamente un presupuesto.  Por lo tanto, es ideal evitar esta situacin, dando la informacin clara al grupo antes de la llegada al restaurante, identificando necesidades especiales como veganos, vegetarianos, celacos o personas con algn tipo de alergia a determinados alimentos.  El hacer un cambio por un capricho de un pasajero se puede entender como un trato preferente y generar inconformidad en los otros usuarios, recuerde, explicar las condiciones generales del servicio antes de empezar la guianza, explique por ejemplo en qu consiste el men del almuerzo y en caso de requerimientos adicionales pueden buscarse opciones pero que si tiene un costo adicional hay que buscar soluciones con equidad.  

      .row.align-items-center.mb-4
        .col-lg-1.col-md-2.col-4(data-aos="fade-right")
          figure.mb-2
            img(src='@/assets/curso/tema2/fig7.svg', alt='Texto que describa la imagen')
        .col-lg-11.col-md-10.col-8(data-aos="fade-left")
          p.mb-0 En el caso que un usuario solicite un cambio en el men, el correcto proceder es que el gua muestre mediante lenguaje no verbal la contrariedad que ocasionan los cambios. Pero muestre inters por conocer las razones para realizar una valoracin de la conveniencia del cambio y comunquelo al contratante (agencia o empresa) para saber si lo autorizan o el usuario debe comunicarse directamente.  

      .row.align-items-center.mb-4
        .col-lg-8(data-aos="fade-right")
          p.mb-4 Por ejemplo, en el caso de las aerolneas el gua puede asistir una parte del trmite (buscar el telfono de contacto o la ubicacin de la oficina ms cercana), pero es el usuario directamente interesado quien debe hacer la gestin, si la compra del tiquete no la hizo a travs de una agencia de viajes o no est dentro de los servicios adquiridos y presenta problema. 
          p.mb-4 Otro caso es que el cambio lo proponga la agencia, empresa o proveedor del servicio, el gua debe ser consciente del enfoque que se le d a la  situacin; as pues, ante un cambio de hotel nunca se debe decir: seores, tengo una mala noticia, es preferible no decir nada y actuar con naturalidad, buscar siempre enfoques positivos y en caso de que se tenga que explicar, argumentar los beneficios.
        .col-lg-4(data-aos="fade-left")
          figure.mb-2
            img(src='@/assets/curso/tema2/img6.jpg', alt='Texto que describa la imagen')
