      .h2 2.3 Tcnicas para recorrido peatonal

    p.mb-4(data-aos="fade-right") Antes de entrar en contacto con los turistas, el gua debe familiarizarse acerca de las caractersticas del servicio requerido por el usuario, as:

    .row.align-items-center.justify-content-center.mb-4
      .col-lg-6(data-aos="fade-right")
        AcordionA.mb-2(tipo="a" clase-tarjeta="tarjeta tarjeta-azul")
          .row(titulo="Solicitar documentacin")
            p.mb-0 Pedir al operador del servicio o a la agencia de viajes, los documentos de arribo del cliente (vuchers, itinerario, programa de estada, pasajes, tickets, cartas y encargos).

          .row(titulo="Aprender nombres")
            p.mb-0 Aprender los nombres de memoria si es un grupo pequeo y el nombre del tour o paquete si es un grupo grande.

          .row(titulo="Preparar su indumentaria")
            p.mb-0 Prepare una vestimenta adecuada, impecable, portando el uniforme de la empresa. Evitar la presencia de olores personales desagradables, portar la tarjeta profesional con el tipo de guianza que se va a desarrollar. Igualmente, mantener limpio y bien peinado el cabello y las uas en buenas condiciones y recortadas. 

          .row(titulo="Consultar")
            p.mb-0 Averiguar por internet o telfono todo lo concerniente con los horarios y costos de los servicios que puedan ser solicitados por el cliente o grupo (especialidades de comidas en restaurantes, museos, horario del comercio, valores de propinas, entre otros).

          .row(titulo="Mapas")
            p.mb-0 Preparar una carpeta o un folder con mapas locales para cada cliente. Se sugiere un mapa de rutas y un plano de la ciudad a recorrer.

          .row(titulo="Itinerario")
            p.mb-0 Sostener una breve charla de puesta en comn con el conductor del vehculo para definir el itinerario y servicio a entregar (Servicio Nacional de aprendizaje SENA, 2018b).
            
