        figure.desaparece.mb-2
          img(src='@/assets/curso/tema2/img7.jpg', alt='Texto que describa la imagen')

    .bg_grad-12
      p.mb-4(data-aos="fade-right") Repaso de los aspectos a tener en cuenta al encontrarse con un grupo de turistas:

      ImagenInfografica.color-acento-botones.mb-5(data-aos="fade")
        template(v-slot:imagen)
          figure
            img(src='@/assets/curso/tema2/info.jpg', alt='')

        .tarjeta.color-acento-botones.p-3(x="79.5%" y="45.5%" numero="+")
          .h5.mb-2 Reconocimiento visual
          p Use un paraguas en alto, un pauelo de color en la mano para que el grupo lo identifique fcilmente como gua. 

        .tarjeta.color-acento-botones.p-3(x="87.2%" y="67.5%" numero="+")
          .h5.mb-2 Presentacin personal
          p Se recomienda dar slo el nombre de pila, para facilitar la memorizacin por parte de los usuarios.

        .tarjeta.color-acento-botones.p-3(x="83%" y="34.7%" numero="+")
          .h5.mb-2 Presentacin recorrido
          p Presente la ruta con los lugares a visitar, la duracin aproximada en cada uno y la hora aproximada de finalizacin del recorrido. 

        .tarjeta.color-acento-botones.p-3(x="38.2%" y="34.5%" numero="+")
          .h5.mb-2 Comportamiento en sitio
          p Diga, por ejemplo, mantenerse siempre unido al grupo, no quedarse ms tiempo del estipulado por el gua, en el cruce de calles o lugares congestionados, ante cualquier duda preguntarle a l, otras relacionadas. 

        .tarjeta.color-acento-botones.p-3(x="39.2%" y="75%" numero="+")
          .h5.mb-2 Enfatice el tiempo
          p Recuerde los horarios para evitar retrasos con relacin al recorrido y revise constantemente el estado de los turistas.
          
      .row.mb-4
        .col-auto(data-aos="fade")
          .sub.p-2
            h4.mb-0.text-center Durante el recorrido
      .row.justify-content-center.align-items-center.mb-4
        .col-lg-4.col-md-6.col-6(data-aos="fade-right")
          figure.mb-4
            img(src='@/assets/curso/tema2/1.jpg', alt='Texto que describa la imagen')     
        .col-lg-8(data-aos="fade-left")
          ul.lista-ul--color
            li
              i.fas.fa-paper-plane 
              p.mb-0 Camine a ritmo adecuado, segn el tipo de grupo. No camine ms de lo que el grupo da, porque un usuario puede extraviarse. Tampoco transmita la sensacin de agobio al grupo, maneje el servicio con calma y serenidad. 
            li
              i.fas.fa-paper-plane 
              p Use los pasos peatonales, esperar a que los semforos vehiculares estn en rojo, desplazar al grupo preferiblemente por andenes anchos. 
            li
              i.fas.fa-paper-plane 
              p.mb-0 En tramos cortos, el gua debe ir adelante pero siempre pendiente del avance del grupo. En caso de que el gua requiera ir hacia atrs del grupo debe dejar un lder con la recomendacin que sigan hasta un punto determinado o parar al llegar a la esquina o cruce de calles.  En caso de que se asigne ms de un gua al servicio, uno deber estar siempre adelante, dirigiendo el trayecto, el otro deber colaborar en el traslado general del grupo. Estas funciones pueden ir rotando. En caso de que un turista realice una pregunta a un gua acompaante, este no debe contradecir o desmedrar la informacin del gua lder, es ideal colaborarse entre s y no disputarse el trabajo. 
            li
              i.fas.fa-paper-plane 
              p.mb-0 Se recomienda que el gua no d informacin de importancia mientras va caminando. Porque solo lo escucharn las personas que tiene inmediatamente a su lado y el resto no, adems caminar y recibir demasiada informacin cansan fsica y psquicamente al turista y genera una experiencia desagradable. Los tramos de traslado se deben tomar como lo que son. En caso de querer dar una informacin, se deber parar nuevamente al grupo, dar la informacin y luego seguir. En caso de que un turista realice una pregunta durante el traslado, si la misma reviste importancia se puede hacer la referencia en la prxima parada y responder a todo el grupo. Por ejemplo Esteban me pregunt algo muy interesante y quiero contarles a todos. En caso de que sea una pregunta que no revista importancia dentro de la informacin general del grupo se responde all mismo al turista que pregunt (Gobierno de provincia de Salta, Ministerio de Cultura y Turismo, 2006). 
    
    .bg_grad-13        
      .row.mb-4
        .col-auto(data-aos="fade") 
          .row 
            .col-auto 
              .sub.p-2
                h4.mb-0.text-center  Paradas para brindar informacin
      .row.align-items-center.justify-content-center.mb-4
        .col-lg-8(data-aos="fade-right")
          ul.lista-ul--color
            li
              i.fas.fa-paper-plane 
              p.mb-0 Bsqueda de un lugar tranquilo donde haya el mnimo de interferencia, tanto acstica como visual. 
            li
              i.fas.fa-paper-plane 
              p Debe prestar atencin a su seguridad y la del grupo, por lo tanto, a la hora de ubicarse debe evitar bloquear el paso de peatones, y a su vez no exponer el grupo al borde de un andn, porque en cualquier momento pasar a una va principal del trnsito de vehculos o una ciclova.
            li
              i.fas.fa-paper-plane 
              p.mb-0 Si es posible, hacer coincidir la parada para la explicacin con la mejor perspectiva fotogrfica con el fin de no perder a los fotgrafos. 
            li
              i.fas.fa-paper-plane 
              p.mb-0 Buscar un escaln, banco o similar para conseguir el contacto visual con todo el grupo, que siempre resulta positivo. 
            li
              i.fas.fa-paper-plane 
              p.mb-0 De explicaciones pertinentes de cara al grupo y de espaldas al monumento, as se puede ver al gua y al recurso turstico al mismo tiempo. Eso le facilitar hacer una correcta sealizacin: a mi derecha, a mi espalda o similar. 
            li
              i.fas.fa-paper-plane 
              p Luego, dando la espalda al atractivo debe ubicar al grupo tratando que sea siempre en semicrculo. En caso de que un atractivo sea tan pequeo que el gua lo tape con su cuerpo, deber ubicar al grupo en semicrculo y el gua se ubicar al costado del grupo.
            li
              i.fas.fa-paper-plane 
              p.mb-0 La informacin debe ser breve, pero interesante para no agotar al turista, no debe empezar la explicacin hasta que prcticamente hayan llegado todas las personas al campo de voz. La informacin que se comparte debe ser verdica. En caso de que el grupo est aburrido hay que cambiar el sentido de la informacin, hacer bromas, comentarios, mantener al grupo interesado de algn modo, sin dejar de lado la importancia de lo que se est comunicando.
