      .row.align-items-center.justify-content-center.mb-4
        .col-lg-4.col-md-6.col-6(data-aos="fade-right")
          figure.mb-2.desaparece
            img(src='@/assets/curso/tema2/3.jpg', alt='Texto que describa la imagen')  
        .col-lg-8(data-aos="fade-left")
          ul.lista-ul--color
            li
              i.fas.fa-paper-plane 
              p.mb-0 En cuanto a las fechas no se debe cansar al pblico con datos especficos, se debe generalizar por siglos, dcadas, movimientos, etapas; exceptuando fechas que son claves en la historia.
            li
              i.fas.fa-paper-plane 
              p Cuando se finaliza la explicacin de un trmino que se considere desconocido para el grupo, se debe preguntar si han entendido. Respetar la opinin de todos por igual. Mantener buenas relaciones con todo el grupo por igual.
            li
              i.fas.fa-paper-plane 
              p.mb-0 Ante preguntas que no se sabe la respuesta, no mentir, aclarar que no se tiene la informacin y comprometerse a hacer lo posible para averiguarlo. El gua debe recordar siempre dirigir al grupo con autoridad (no autoritarismo), esto le permite mantener el control en todo momento.
            li
              i.fas.fa-paper-plane 
              p.mb-0 Se finaliza la exposicin por el punto donde se deba continuar la visita. De las indicaciones de direccin antes de retomar el desplazamiento del grupo. 
            li
              i.fas.fa-paper-plane 
              p.mb-0 Antes de entrar al interior de un atractivo (museo, iglesia, calle), no se debe olvidar dar el nombre completo del mismo para evitar que los turistas se pierdan. Si el recorrido en el interior es libre, antes de ingresar se debe pautar la hora de encuentro y el lugar (que en general es el mismo por donde se ingresa). 
            li
              i.fas.fa-paper-plane 
              p Para la descripcin de edificios:  evite utilizar la mano o el brazo. En concreto, no se debe sealar. En la descripcin del atractivo se debe contar ordenadamente:  generalidades, la historia, la descripcin arquitectnica externa, la descripcin interna del atractivo, y las actualidades del mismo. Para describir arquitectnicamente un edificio en su parte exterior, o sea su fachada, se debe hacer: de abajo hacia arriba y de izquierda a derecha. 
            li
              i.fas.fa-paper-plane 
              p.mb-0 En el caso de que no se pueda guiar dentro del edificio se debe dar la informacin por adelantado, seleccionando los atractivos interiores de mayor relevancia y luego autorizar el ingreso al mismo.  
      p.mb-4(data-aos="fade-right") Algunas recomendaciones para la guianza de atractivos tursticos de inters general son los siguientes:

