          p.mb-4 El tono de voz debe cubrir correctamente la zona donde se encuentra el grupo, si el gua gira hacia el monumento, su campo de voz no cubre al auditorio y la consecuencia es que el cliente ve perfectamente la nuca del gua al mismo tiempo que deja de orlo, con lo que se cometen simultneamente dos errores, uno con respecto al lenguaje no verbal y otro con lenguaje oral. 
        p.mb-4 El tono de voz se debe adecuar al tipo de grupo y al lugar donde se est guiando. No es lo mismo un grupo de ancianos, que uno de jvenes o nios. En cualquiera de los casos se debe modular bien y hablar claro. El grupo nunca debe esforzarse por escuchar bien. En lugares con demasiado trnsito vehicular o excesivo ruido, el gua puede apoyarse de un altavoz y ubicarse en una zona ms alto que el grupo para que lo escuchen mejor. Ante todo, evite gritar, la correcta modulacin de la voz permite mantener el inters del grupo guiado. 
