    #t_2_4.titulo-segundo.color-acento-contenido(data-aos="fade")
      .h2 2.4 Tcnicas para el recorrido en espacio naturaleza

    p.mb-4(data-aos="fade-right") Con el fin de mejorar los estndares de la prestacin de servicios a caminantes, optimizar los recursos y homologar buenas prcticas en torno al turismo de naturaleza, se aconseja seguir los siguientes puntos: 

    .row.align-items-center.mb-4 
      .col-lg-4(data-aos="fade-right")
        figure.mb-4
          img(src="@/assets/curso/tema2/fig8.svg", alt="alt")
      .col-lg-8(data-aos="fade-left")
        ul.lista-ul--color
          li
            i.fas.fa-paper-plane 
            p.mb-0 Realizar un pre recorrido para determinar los lugares propicios para los descansos, hidratacin, reagrupacin y lectura del paisaje. Adems, verificar que haya una adecuada sealizacin del sendero y solicitar las acciones de mantenimiento, en caso necesario.
          li
            i.fas.fa-paper-plane 
            p.mb-0 Contactar guas locales que acompaen al reconocimiento del terreno para determinar rutas alternas en caso de contingencias.
          li
            i.fas.fa-paper-plane 
            p.mb-0 Establecer contacto con las autoridades locales para informar sobre la actividad de caminata que se realizar.
          li
            i.fas.fa-paper-plane 
            p.mb-0 Elaborar croquis del recorrido georreferenciado con perfil altimtrico y convenciones.
          li
            i.fas.fa-paper-plane 
            p.mb-0 Establecer un plan de manejo de los residuos slidos.
          li
            i.fas.fa-paper-plane 
            p.mb-0 Informar a los turistas la indumentaria y opciones para hidratacin y alimentacin durante la actividad. 
          li
            i.fas.fa-paper-plane 
            p.mb-0 Determinar grado de dificultad y tipologa de la caminata 

    .bg_grad-14
      p.mb-4(data-aos="fade-right") La dificultad de una caminata se mide por tiempo y por niveles adicionales como se ver a continuacin: 
      .row.align-items-center.mb-4 
        .col-lg-9(data-aos="fade-right")
          TabsA.color-acento-contenido.mb-5        
            .tarjeta.clr-paleYellow.p-4(titulo="Duracin de 3 a 5 horas")
              h4.mb-4 Duracin de 3 a 5 horas
              p.mb-0 Longitud de hasta 7 km. Terrenos llanos o ligeramente ondulados. La temperatura oscila entre los 18 y 25 grados centgrados. Apta para principiantes y quienes disfrutan de la contemplacin del paisaje. 

            .tarjeta.clr-paleYellow.p-4(titulo="Duracin de 5 a 7 horas")
              h4.mb-4 Duracin de 5 a 7 horas
              p.mb-0 Longitud entre 7 km y 10 km. Terrenos mixtos, de pocas ondulaciones o inclinacin media. La temperatura oscila entre los 18 y 28 grados centgrados. Apta para quienes regularmente tienen actividad fsica y disfrutan de la contemplacin de paisajes. 

            .tarjeta.clr-paleYellow.p-4(titulo="Duracin de 6 a 8 horas")
              h4.mb-4 Duracin de 6 a 8 horas
              p.mb-0 Longitud entre 10 km y 13 km. Terrenos mixtos, de ondulaciones e inclinaciones pronunciadas. La temperatura oscila entre los 10 y 30 grados centgrados. Apta para quienes regularmente tienen actividad fsica y ya tienen experiencia en caminatas y senderismo. 

            .tarjeta.clr-paleYellow.p-4(titulo="Duracin de 7 a 9 horas")
              h4.mb-4 Duracin de 7 a 9 horas
              p.mb-4 Longitud entre 13 km y 18 km. Terrenos mixtos con mayor presencia de accidentes geogrficos escarpados con variedad topogrfica. La temperatura oscila entre los 10 y 38 grados centgrados.
              p.mb-0 Apta para personas con muy buen estado fsico, que tienen experiencia en senderismo.

            .tarjeta.clr-paleYellow.p-4(titulo="Duracin 9 a 12 horas")
              h4.mb-4 Duracin 9 a 12 horas
              p.mb-4 Longitud entre 18 km y 25 km. Terrenos agrestes con accidentes geogrficos de ascensos y descensos pronunciados que exigen al caminante sortear diversos obstculos naturales.
              p.mb-0 La temperatura oscila entre los 7 y 40 grados centgrados. Apta nicamente para personas con muy buen estado fsico, de espritu aventurero, que tienen alta experiencia en senderismo.
        .col-lg-3(data-aos="fade-left")
          figure
            img(src='@/assets/curso/tema2/fig9.svg', alt='')

    p.mb-4(data-aos="fade-right") Al iniciar y durante la caminata por favor tener en cuenta lo siguiente:  

    .row.justify-content-center.align-items-center.mb-4 
      .col-lg-4.col-md-6.col-6(data-aos="fade-right")
        figure.mb-4
          img(src='@/assets/curso/tema2/img9.jpg', alt='')
      .col-lg-8(data-aos="fade-left")
        ul.lista-ul--color
          li
            i.fas.fa-paper-plane 
            p.mb-0 Orientar a los caminantes sobre estiramientos musculares antes, durante y despus.
          li
            i.fas.fa-paper-plane 
            p.mb-0 Presentacin del equipo de trabajo y funciones. 
          li
            i.fas.fa-paper-plane 
            p.mb-0 Orientar a los participantes acerca de las normas a seguir, especficamente las relacionadas con No Dejar Rastro.
          li
            i.fas.fa-paper-plane 
            p.mb-0 Promover el autocuidado al caminante y respeto por los lugareos, siendo amables, considerados con su cultura y sus cultivos.
          li
            i.fas.fa-paper-plane 
            p.mb-0 Procurar paradas tcnicas (recuperacin, ilustracin, reagrupamiento, en lugares significativos).
          li
            i.fas.fa-paper-plane 
            p.mb-0 Conservar actitud ejemplarizante, adquiriendo un comportamiento de prevencin y respeto con el entorno. Por ejemplo, evite tocar o desprender ramas para la demostracin de una especie, puede apuntar con el bastn de caminata o un lser. Para el avistamiento de fauna revise el material complementario manual de operacin turstica para el avistamiento de aves.
          li
            i.fas.fa-paper-plane 
            p.mb-0 Estar atentos durante todo el recorrido dejar puertas y cercados como se encontraron.
          li
            i.fas.fa-paper-plane 
            p.mb-0 Orientar al caminante sobre el manejo de residuos slidos.
          li
            i.fas.fa-paper-plane 
            p.mb-0 Al finalizar el recorrido realizar ejercicios de estiramiento y vuelta a la calma.
          li
            i.fas.fa-paper-plane 
            p.mb-0 Llamar a lista antes, durante y despus del recorrido. 
          li
            i.fas.fa-paper-plane 
            p.mb-0 Llamar a las autoridades a confirmar que termin el recorrido con o sin novedades (Gobernacin de Antioquia, 2015).
    separador 

    #t_2_5.titulo-segundo.color-acento-contenido(data-aos="fade")
      .h2 2.5 Tcnica para recorrido panormico

    p.mb-5(data-aos="fade-right") En este caso el servicio que se presta es dentro de un vehculo; si el servicio es de ms de un da es ideal que el gua de turismo prepare msica y pelculas para los desplazamientos. Para el caso de la msica, si el grupo est compuesto por estudiantes, el gua prcticamente se limitar a colocar la msica que le indiquen, mientras que si es un grupo convencional de mediana edad, la msica ser un tema que deber cuidar por su gran influencia en los estados de nimo por ello se aconseja,  en la maana colocar msica suave, despus de la primera parada de la maana una ms animada, ojal versiones cantadas (salsa, rumba, tropical), al entrar en un nueva regin o ciudad pueden colocar msicas representativas de la zona. 
    br
    .row.mb-4(data-aos="fade")
      .col-lg-12
        .bloque-texto-g.fondo2.p-3.p-sm-4.p-md-4.my-4.otrobloque2
          .bloque-texto-g__img(
            :style="{'background-image': `url(${require('@/assets/curso/tema2/9.png')})`}"
          )
          .bloque-texto-g__texto.py-5.px-5
            p Respecto a las pelculas es conveniente que el gua disponga de su propia videoteca de viaje. Al confiar la responsabilidad a la empresa de transporte, puede que las copias estn en mal estado, que sean pelculas no adecuadas a la tipologa de los viajeros o que inexplicablemente, a aquella pelcula excelente le falten los ltimos diez minutos. D prioridad a pelculas ligeras, principalmente comedias, adecuadas a todos los pblicos, sin demasiada sangre, ni violencia o escenas de sexo que puedan incomodar a los pasajeros.
    p.mb-4(data-aos="fade-right") Durante el <b> <FONT COLOR="#FD6300"> recorrido panormico </FONT> </b> se determina que el gua de turismo tenga en cuenta:

    .slyderB_bg_01.p-4.mb-5(data-aos="fade-up")
      SlyderA(tipo="b")
        .row.justify-content-center.align-items-center
          .col-lg-4.col-md-12.col-12.mb-4
            .number(data-number="01")
              p.mb-0 Pedir la colaboracin de los viajeros en sentarse lo ms rpido posible por la comodidad de todos los pasajeros; al no bloquear el pasillo y para poder verificar que todos los usuarios estn abordo.
          .col-lg-8.col-md-8.col-8
            figure
              img(src='@/assets/curso/tema2/slyder1.png')

