cance, muestre su inters por lo sucedido antes que llamarle la atencin frente al grupo sin averiguar lo ocurrido. Si no les ha sucedido nada de gravedad, de manera educada en privado, haga ver las consecuencias en el itinerario, para que sienta la necesidad de pedir una disculpa y evitar repetir la tardanza. Si a pesar de las llamadas de atencin en privado continan llegando tarde, es conveniente buscar un motivo que obligue a ello, eximiendo al gua de responsabilidad, por ejemplo, la imposibilidad de estar esperndolos en determinados lugares por las reglas del destino o atractivo, el horario inamovible de un vuelo, entre otras. Para dar mayor credibilidad se procede a dar el precio y hora de los transportes pblicos hacia el prximo destino. Si se amenaza, hay que ser conscientes de que se debe cumplir, por tanto, solo recurrir a ella cuando no haya mucha dificultad en llegar al destino, por ejemplo, en los traslados dentro de la misma ciudad.")])])]),_c('div',{staticClass:"row mb-4"},[_c('div',{staticClass:"col-auto",attrs:{"data-aos":"fade"}},[_c('div',{staticClass:"sub p-2"},[_c('h4',{staticClass:"mb-0 text-center"},[_vm._v("Responsabilidad de los pasajeros")])])])]),_c('div',{staticClass:"row align-items-center mb-4"},[_c('div',{staticClass:"col-lg-8",attrs:{"data-aos":"fade-right"}},[_c('p',{staticClass:"mb-4"},[_vm._v("La llamada de despertar en el alojamiento se hace con el propsito de despertar a los viajeros con una llamada desde la recepcin, pero cuando por algn motivo la recepcin olvida despertar a un viajero, el viajero cree no ser culpable de su tardanza. El gua debe dejar claro que es responsabilidad de los viajeros el despertarse y que la llamada tan slo constituye un complemento para asegurarse. Por otro lado, respecto a las pertenencias, es importante aclarar que ni el gua, ni el equipo de trabajo como gua o proveedores pueden ser custodios de estas. Esto ayuda a que el usuario sea consciente de lo que realmente necesita. En caso de que se requiera que los usuarios dejen algunos elementos personales en el vehculo, es ideal dejarlos en custodia o ubicarlos de tal forma que queden ocultos de los ladrones o tambin ubicarlos en la bodega o bal, as de esta manera en caso de infortunio como un robo puedan ser cubiertos por el seguro de viajes, el cual tambin es responsabilidad del viajero. ")])]),_c('div',{staticClass:"col-lg-4",attrs:{"data-aos":"fade-left"}},[_c('figure',{staticClass:"mb-2"},[_c('img',{attrs:{"src":require("@/assets/curso/tema2/img5.jpg"),"alt":"Texto que describa la imagen"}})])])]),_c('div',{staticClass:"row mb-4"},[_c('div',{staticClass:"col-auto",attrs:{"data-aos":"fade"}},[_c('div',{staticClass:"sub p-2"},[_c('h4',{staticClass:"mb-0 text-center"},[_vm._v("Los cambios")])])])]),_c('div',{staticClass:"row align-items-center mb-4"},[_c('div',{staticClass:"col-lg-4",attrs:{"data-aos":"fade-right"}},[_c('figure',{staticClass:"mb-2"},[_c('img',{attrs:{"src":require("@/assets/curso/tema2/fig6.svg"),"alt":"Texto que describa la imagen"}})])]),_c('div',{staticClass:"col-lg-8",attrs:{"data-aos":"fade-left"}},[_c('p',{staticClass:"mb-0"},[_vm._v("En los meses de mayor afluencia turstica, ni a los meseros, ni a los cocineros u otros prestadores de servicios les agrada tener que realizar cambios de ltima hora en lo ofertado y aprobado. El cambio de un men, por ejemplo, supone un costo adicional para el restaurante que ya ha preparado el total de mens reservados en funcin de lo acordado; aunque es posible solicitar cambios siempre y cuando no afecte ampliamente un presupuesto. Por lo tanto, es ideal evitar esta situacin, dando la informacin clara al grupo antes de la llegada al restaurante, identificando necesidades especiales como veganos, vegetarianos, celacos o personas con algn tipo de alergia a determinados alimentos. El hacer un cambio por un capricho de un pasajero se puede entender como un trato preferente y generar inconformidad en los otros usuarios, recuerde, explicar las condiciones generales del